let util = {
  //
  getTime: function (date) {
    date = date || new Date();
    function formatNumber(n) {
      n = n.toString();
      return n[1] ? n : "0" + n;
    }
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var day = date.getDate();

    var hour = date.getHours();
    var minute = date.getMinutes();
    var second = date.getSeconds();

    return (
      [formatNumber(year), formatNumber(month), formatNumber(day)].join("-") +
      " " +
      [formatNumber(hour), formatNumber(minute), formatNumber(second)].join(":")
    );
  },

  getDate: function (date) {
    return this.getTime(date).split(" ")[0];
  },

  addDays: function (date, days) {
    return this.addTimes(date, 60, 60, 24, days);
  },
  addHours: function (date, hours) {
    return this.addTimes(date, 60, 60, hours, 1);
  },
  addMinutes: function (date, minutes) {
    return this.addTimes(date, 60, minutes, 1, 1);
  },
  addSeconds: function (date, secs) {
    return this.addTimes(date, secs, 1, 1, 1);
  },
  addTimes: function (date, s, m, h, days) {
    //new
    date = date || new Date();
    //
    var mt = date.getTime();
    //7
    var premt = mt + 1000 * s * m * h * days;
    var dt = new Date(premt);
    return dt;
  },

  //
  //: 
  //       :yyyy :MM : dd  (24):HH (12):hh :mm :ss
  //: 
  formatTime: function (obj, arg) {
    function fixNum(num) {
      num = parseInt(num);
      if (num < 10) return "0" + num;
      return num;
    }
    function to12Hour(num) {
      num = parseInt(num);
      if (num > 12) {
        return num - 12;
      }
      return num;
    }
    if (!obj) obj = new Date();
    var year = obj.getFullYear();
    var month = fixNum(obj.getMonth() + 1);
    var date = fixNum(obj.getDate());
    var hour24 = fixNum(obj.getHours());
    var hour12 = fixNum(to12Hour(obj.getHours()));
    var minute = fixNum(obj.getMinutes());
    var second = fixNum(obj.getSeconds());
    if (!arg) {
      return "" + year + month + date + hour24 + minute + second;
    }
    arg = arg.replace(/yyyy/g, year);
    arg = arg.replace(/MM/g, month);
    arg = arg.replace(/dd/g, date);
    arg = arg.replace(/HH/g, hour24);
    arg = arg.replace(/hh/g, hour12);
    arg = arg.replace(/mm/g, minute);
    arg = arg.replace(/ss/g, second);
    return arg;
  },
  getQueryString: function (queryStringName) {
    var returnValue = "";
    var URLString = window.location.href;
    var serachLocation = -1;
    var queryStringLength = queryStringName.length;
    do {
      serachLocation = URLString.indexOf(queryStringName + "=");
      if (serachLocation != -1) {
        if (
          URLString.charAt(serachLocation - 1) == "?" ||
          URLString.charAt(serachLocation - 1) == "&"
        ) {
          URLString = URLString.substr(serachLocation);
          break;
        }
        URLString = URLString.substr(serachLocation + queryStringLength + 1);
      }
    } while (serachLocation != -1);
    if (serachLocation != -1) {
      var seperatorLocation = URLString.indexOf("&");
      if (seperatorLocation == -1) {
        returnValue = URLString.substr(queryStringLength + 1);
      } else {
        returnValue = URLString.substring(
          queryStringLength + 1,
          seperatorLocation
        );
      }
    }
    return returnValue;
  },
  isMobile() {
    let flag = navigator.userAgent.match(
      /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
    );
    return !!flag;
  },
  HTMLDecode(text) {
    var temp = document.createElement("div");
    temp.innerHTML = text;
    var output = temp.textContent;
    temp = null;
    return output;
  },
  replaceTag(str) {
    str = str.replace(/^\<p\s*\>/, "").replace(/\<\/p\s*\>$/, "");
    return str;
  },
  //  pc, ,
  fixRem(w) {
    if (this.isMobile()) {
      return w;
    }
    if (window.devicePixelRatio == 1) {
      return w * 2;
    }
    return w;
  },
  px2rem(w) {
    return this.fixRem(window.lib.flexible.px2rem(parseFloat(w))) + "rem";
  },
  base64decode(str) {
    var base64Chars =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/".split(
        ""
      );
    var bc = str.replace(/=/g, "").split(""),
      y,
      z,
      c,
      b,
      bs = "";
    for (y in bc) {
      c = "";
      for (z in base64Chars) {
        if (bc[y] == base64Chars[z]) {
          c = z;
          break;
        }
      }
      if (c == "") return "";
      b = Number(c).toString(2);
      if (b.length < 6) {
        b = ("00000" + b).slice(-6);
      }
      bs += b;
    }
    var j = Math.floor(bs.length / 8),
      i,
      ha = "",
      hb;
    for (i = 0; i < j; i++) {
      hb = parseInt(bs.slice(i * 8, i * 8 + 8), 2).toString(16);
      if (hb.length < 2) hb = ("00" + hb).slice(-2);
      ha += "%" + hb;
    }
    return decodeURIComponent(ha);
  },

  getListSearchOption: function () {
    // 
    var sDate = util.formatTime(util.addDays(new Date(), 0), "yyyy-MM-dd");
    // var sDateMonth = util.formatTime(util.addDays(new Date(), -30), "yyyy-MM-dd");
    // 
    var eDate = util.formatTime(util.addDays(new Date(), 1), "yyyy-MM-dd");
    // 
    // var firstDate = util.formatTime(new Date((new Date()).setDate(1)),  "yyyy-MM-dd");
    var count = 10;
    var h = window.screen.height;
    if (h > 900) {
      count = 15;
    }
    return {
      starttime: sDate,
      endtime: eDate,
      pagesize: count,
    };
  },
  downloadFile: function (res) {
    let blob = new Blob([res.data], {
      type: `application/octet-stream;charset=utf-8`,
    });
    // headsfilename
    let downloadElement = document.createElement("a");
    // 
    let href = window.URL.createObjectURL(blob);
    downloadElement.href = href;
    // 
    downloadElement.download = res.name;
    document.body.appendChild(downloadElement);
    // 
    downloadElement.click();
    // 
    document.body.removeChild(downloadElement);
    // blob
    window.URL.revokeObjectURL(href);
  },
  //  ,,
  resetObject: function(o) {
    Object.keys(o).forEach(function(k){
      if(typeof(o[k]) == "number") {
        o[k] = 0;
      }
      else if(typeof(o[k]) == "string") {
        o[k] = "";
      }
      else if(Object.prototype.toString.call(o[k]) == "[object Array]") {
        o[k] = [];
      }
    })
  },
  // 
  convertTime(time) {
    let minute = Math.floor(time / 60);
    let second = Math.round(time % 60);
    return `${minute}:${second < 10 ? "0" + second : second}`;
  },
  //  
  async connectReverbNode(node,  reverbSetting, audioContext) {
    if(!reverbSetting) {
      return {
        outputNode: node,
        reverbNodes: null,
        disconnectReverb: () => {}
      }
    }
    const optionsMap = {
      effect1: {
          delayTimes: [0.02, 0.03],//  ()
          feedbackGains: [0.5, 0.4], // 
          dryGain: 0.6, // 
          wetGain: 0.4, // 
          highCutFrequency: 5000,  //  (Hz)
          preDelayTime: 0.02  //  ()
      },
      // 
      effect2: {
          delayTimes: [0.05, 0.07],
          feedbackGains: [0.6, 0.5],
          dryGain: 0.5,
          wetGain: 0.5,
          highCutFrequency: 4000,
          preDelayTime: 0.03
      },
      effect3: {
        delayTimes: [0.02, 0.025], //  ()
        feedbackGains: [0.6, 0.5], // 
        dryGain: 0.7, // 
        wetGain: 0.3, // 
        highCutFrequency: 4000, // 
        preDelayTime: 0.03  //  ()
      }
  };

  // const settings = optionsMap['effect' + effectType];
  const settings = reverbSetting;

  const dryGainNode = audioContext.createGain();//
  dryGainNode.gain.value = settings.dryGain;

  const wetGainNode = audioContext.createGain();//
  wetGainNode.gain.value = settings.wetGain;

  const preDelayNode = audioContext.createDelay();  //
  preDelayNode.delayTime.value = settings.preDelayTime;

  const highCutFilter = audioContext.createBiquadFilter(); //
  highCutFilter.type = 'lowpass';
  highCutFilter.frequency.value = settings.highCutFrequency;

  const leftChannelGain = audioContext.createGain();
  const rightChannelGain = audioContext.createGain();

  const delayNodeLeft = audioContext.createDelay(); //
  delayNodeLeft.delayTime.value = settings.delayTimes[0];
  const feedbackGainLeft = audioContext.createGain();
  feedbackGainLeft.gain.value = settings.feedbackGains[0];//
  delayNodeLeft.connect(feedbackGainLeft);
  feedbackGainLeft.connect(delayNodeLeft);



  const delayNodeRight = audioContext.createDelay();
  delayNodeRight.delayTime.value = settings.delayTimes[1];
  const feedbackGainRight = audioContext.createGain();
  feedbackGainRight.gain.value = settings.feedbackGains[1];
  delayNodeRight.connect(feedbackGainRight);
  feedbackGainRight.connect(delayNodeRight);


  const merger = audioContext.createChannelMerger(2);
  delayNodeLeft.connect(merger, 0, 0); // 
  delayNodeRight.connect(merger, 0, 1); // 

  // 
  const outputNode = audioContext.createGain();

  // 
  node.connect(dryGainNode);
  node.connect(wetGainNode);
  wetGainNode.connect(preDelayNode);
  preDelayNode.connect(highCutFilter);
  highCutFilter.connect(leftChannelGain);
  highCutFilter.connect(rightChannelGain);
  leftChannelGain.connect(delayNodeLeft);
  rightChannelGain.connect(delayNodeRight);
  dryGainNode.connect(outputNode);
  merger.connect(outputNode);

  let reverbNodes = {
    dryGainNode,
    wetGainNode,
    preDelayNode,
    highCutFilter,
    delayNodeLeft,
    delayNodeRight,
    feedbackGainLeft,
    feedbackGainRight
  }

  // 
  return {
      outputNode, // 
      reverbNodes,
      disconnectReverb: () => { // 
          dryGainNode.disconnect();
          wetGainNode.disconnect();
          preDelayNode.disconnect();
          highCutFilter.disconnect();
          leftChannelGain.disconnect();
          rightChannelGain.disconnect();
          delayNodeLeft.disconnect();
          delayNodeRight.disconnect();
          merger.disconnect();
      }
    }
  }  
};
export default util;
