<template>
  <div class="container compose">
    <van-nav-bar
      title=""
      left-text=""
      @click-left="onBack()"
      right-text=""
      @click-right="onSetting"      
    />
    <!-- <audio
      ref="audio"
      style="display: none"
      @ended="finish"
      @loadedmetadata="onLoadedMetadata"
    ></audio> -->
    <div class="content content-compose">
      <div class="c-e-name c-e-name-crop">{{ musicName }}</div>
      <div class="c-e-ctrlbox">
        <div class="c-e-ctrlitem">
          <van-icon
            v-show="playing"
            name="pause-circle-o"
            class="c-e-ctrlicon"
            @click="onPause"
          />
          <van-icon
            name="play-circle"
            class="c-e-ctrlicon"
            v-show="!playing"
            @click="onPlay"
          />
        </div>
        <div class="c-e-sliderbox-time">
          {{ util.convertTime(curTime) }}
        </div>
        <div class="c-e-sliderbox">
          <van-slider v-model="curTime" @change="onDurationChange" :max="audioDuration" @drag-start="dragging = true" @drag-end="dragging = false" />
        </div>
        <div class="c-e-sliderbox-time">{{ util.convertTime(audioDuration) }}</div>
      </div>
      <div class="c-co-lrcbox">
        {{ curLrc }}
      </div> 
      <div class="c-e-title">
        <span></span><span>{{ accompanyVal }}</span>
      </div>
      <div class="c-e-voicebox c-co-voicebox">
        <van-slider min="0" max="100" v-model="accompanyVal" @change="onAccompanyVoiceChange" />
      </div>      
      <div class="c-e-title">
          <span></span>
          <van-switch v-model="store.compress" />
        </div>      
      <div class="c-co-imgbox" v-show="imgList.length">
        <div class="c-co-imgtitle">
          <span>images:</span>
          <van-radio-group v-model="orient" direction="horizontal" @change="onOrientChange">
            <van-radio name="0">landscape</van-radio>
            <van-radio name="1">portrait</van-radio>
          </van-radio-group>
        </div>
        <div :class="orient == '0' ? 'c-co-imglist': 'c-co-imglist portrait'">
          <div class="c-co-imgitem" v-for="(item,index) of imgList" :key="index" :style="getBg(item)">
            <van-icon name="close" class="c-co-imgrev" @click="onRemoveImg(item, index)"/>
          </div>
        </div>
      </div>
      <div class="c-e-editbox c-co-editbox">
        <!--  -->
        <div class="c-co-item" v-for="(item, index) of filelist" :key="index">
          <audio :src="item.file.url" ref="setItemRef" class="c-co-audio" />
          <div class="c-co-namebox">
            <div class="c-co-itemname">{{ item.file.name.replace(/\.(mp3|wav)$/i, "") }}</div>
          </div>
          <div class="c-co-volbox">
            <span class="c-co-volname">:&nbsp;<b>{{ item.volume }}</b></span>
            <van-slider
              class="c-co-volslider"
              v-model="item.volume"
              @change="onVolumeChange(item, index)"
            />
          </div>
          <div class="c-co-volbox">
            <span class="c-co-volname">:&nbsp;<b>{{ item.delay }}</b></span>
            <van-slider
              min="-2"
              max="2"
              step="0.1"
              class="c-co-volslider c-co-volslider-delay"
              v-model="item.delay"
              @change="onDelayChange(item, index)"
            />
          </div>    
          <div class="c-co-volbox">
            <div class="c-co-volname"></div>
            <div class="c-co-reverbbox">
              <!-- <van-radio-group v-model="item.reverb" direction="horizontal" @change="onReverbChange(item, index)">
                <van-radio class="c-co-reverbitem" :name="0">none</van-radio>
                <van-radio class="c-co-reverbitem" :name="1">reverse</van-radio>
                <van-radio class="c-co-reverbitem" :name="2">reverse2</van-radio>
                <van-radio class="c-co-reverbitem" :name="3">reverse3</van-radio>
              </van-radio-group> -->
              <van-button v-show="item.reverb" plain type="primary" size="small" class="c-e-cusbtn" @click="onReverbSetting(item, index)"></van-button>
              <van-switch  v-model="item.reverb" @change="(b)=>{onReverbChange(b, item, index)}" />
            </div>
          </div>
          <van-icon name="close" class="c-co-remove" @click="onVoiceRemove(item, index)"/>
        </div>
      </div>
      <div class="c-e-btnbox c-co-btnbox">
        <div class="c-co-innerbox">
          <input type="file" multiple ref="imgFile" style="display: none" @change="onImgFileSelect" />
          <!--  , :-) -->
          <!-- <van-button plain class="c-e-btn" type="warning" @click="onImgAdd">image</van-button> -->
          <input type="file" ref="audioFile" style="display: none" @change="onAudioFileSelect" />
          <van-button class="c-e-btn" type="primary" @click="onAudioAdd"></van-button>
          <van-button round class="c-e-btn c-e-btn-r" type="success" @click="compose"></van-button>
        </div>
      </div>
    </div>
  </div>
  <SettingDialog v-model:visible="settingShow" :data="curReverbData"/>
  <ReverbDialog ref="rbDlg" @change="onReverbDetailChange" @close="curReverbItem=null"/>

</template>
<script>
// @ is an alias to /src
import { ref } from "vue";
import useStore from "../store";
import SettingDialog from "../components/SettingDialog";
import ReverbDialog from "../components/ReverbDialog";
import util from "../common/js/util.js";
import { renderProgress }from "../common/js/progress.js";
import downloadAudio from "../common/js/downloadAudio.js";
import {stopDownload} from "../common/js/downloadAudio.js";
import { showToast } from "vant";
import createPizzicato from "../common/js/Pizzicato.js";
let Pizzicato;

export default {
  name: "ComposeView",
  components: {SettingDialog, ReverbDialog},
  setup() {
    Pizzicato = createPizzicato(new (window.AudioContext || window.webkitAudioContext)());
    console.log("compose.......")
    // store,
    const store = useStore();
    const rbDlg = ref(null);
    return { store, util, rbDlg };
  },
  data() {
    return {
      audioContext: null,
      offlineAudioCtx: null,
      musicName: "",
      // 
      audioDuration: 0,
      // 
      curTime: 0,
      // 
      curLrc: "",
      dragging: false,
      accompanyVal: 0,
      playing: false,
      orient: "0",
      settingShow: false,
      remainTime: 0,
      imgList: [
      ],
      filelist: [
      ],
      rbSettingShow: false,
      curReverbData: {},
    };
  },
  mounted() {
    if(!this.store.banzou) {
      alert("");
      this.$router.replace("/");
      return;
    }
    
    this.audioContext = Pizzicato.context;
    console.log(this.audioContext)
    this.init(this.store.banzou);
    document.addEventListener('visibilitychange', this.setVisibilyChange);    
  },
  unmounted() {
    document.removeEventListener('visibilitychange', this.setVisibilyChange); 
    if(this.rTimer) {
      cancelAnimationFrame(this.rTimer);
    }
    for(let i = 0; i < this.filelist.length; i++) {
      const item = this.filelist[i];
      if(item.delayPlayTimer) {
        clearTimeout(item.delayPlayTimer);
        item.delayPlayTimer = null;
      }
      item.voiceSource.disconnect();
      item.voiceSource.buffer = null;       
    }
    this.fileList = [];
    try {
      this.bgSource.disconnect();
      this.bgSource.buffer = null;
    }catch(e){}

    try {
      this.audioContext.close();
    }catch(e){
      console.log(e);
    }
    try {
      if(this.offlineAudioCtx) {
        if(this.offlineAudioCtx.state == "running") {
          this.offlineAudioCtx.suspend(this.offlineAudioCtx.currentTime);
        }
        this.offlineAudioCtx = null;
      }
    }catch(e){
      console.log(e);
    }
    console.log("hide loading")
    this.store.hideLoading();
    this.store.hideProgess();
    stopDownload();
    renderProgress.stop();
    Pizzicato = null;
  },
  watch: {
    // 
    "store.banzouVolume": {
      handler() {
        if (this.audioContext) {
          this.accompanyVal = parseInt(this.store.banzouVolume * 100);
          this.bgGain.gain.value = this.store.banzouVolume;          
        }
      },
    },
  },
  methods: {
    setVisibilyChange() {
      if (document.visibilityState === 'visible') {
        try {
          this.audioContext.suspend();
        }
        catch(e){}
      }
      else if (document.visibilityState === 'hidden') {
        this.doStop();
      }
    },
    onReverbSetting(item, index){
      this.rbDlg.show({        
        time: item.reverbNode.time, 
        decay: item.reverbNode.decay, 
        mix: item.reverbNode.mix
      });
      this.curReverbItem = item;
    },
    async init(accompanimentBlob) {
      this.musicName = this.store.banzou.name.replace(/\.mp3$/i, "");
      this.store.showLoading();
      // 1. 
      const accompanimentBuffer = await this.loadAudioData(this.audioContext,accompanimentBlob);
      const recordDuration = accompanimentBuffer.duration;
      this.audioDuration = recordDuration;
      // 2. 
      const sampleRate = this.audioContext.sampleRate;
      const totalLength = recordDuration * sampleRate;

      const bgBuffer = this.audioContext.createBuffer(2, totalLength, sampleRate); // 

      // 3. 
      bgBuffer.getChannelData(0).set(accompanimentBuffer.getChannelData(0).subarray(0, totalLength));
      let n = 0;
      if(accompanimentBuffer.numberOfChannels > 1){
        n = 1;
      }      
      bgBuffer.getChannelData(1).set(accompanimentBuffer.getChannelData(n).subarray(0, totalLength));
      
      const bgSource = this.audioContext.createBufferSource();
      bgSource.buffer = bgBuffer;

      this.bgGain = this.audioContext.createGain();
      this.accompanyVal = parseInt(this.store.banzouVolume * 100);
      this.bgGain.gain.value = this.store.banzouVolume;
      // this.accompanyVal = Math.round(this.bgGain.gain.value * 100);
      bgSource.connect(this.bgGain);  
      Tone.setContext(this.audioContext);
      var pitchShift = new Tone.PitchShift();
      pitchShift.pitch = this.store.banzouPitch;  
      Tone.connect(this.bgGain, pitchShift);    
      // this.bgGain.connect(this.audioContext.destination);
      Tone.connect(pitchShift, this.audioContext.destination);

     
      this.bgSource = bgSource;

      this.remainTime = this.audioContext.currentTime;
      
      //this.setCurTime();
      if(this.store.lrc) {
        await this.store.readLrcText()
      }

      // 
      this.audioContext.suspend();
      bgSource.start(); 
      this.store.hideLoading();
      // voiceSource.start();
    },  
    loadAudioData(audioContext, blob) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => {
          audioContext
            .decodeAudioData(reader.result)
            .then((audioBuffer) => resolve(audioBuffer))
            .catch((err) => reject(err));
        };
        reader.onerror = () => reject(reader.error);
        reader.readAsArrayBuffer(blob);
      });
    }, 
    setCurTime() {
      let n = this.audioContext.currentTime - this.remainTime;
      if(n >= this.audioDuration){
        this.doStop(true);
        return;
      }
      if(!this.dragging) {
        this.curTime = n;
      }
      this.onTimeUpdate();
      //console.log("curTime-> ", this.curTime,  this.store.banzouLength);
      this.rTimer = requestAnimationFrame(this.setCurTime);
    },    
    onAccompanyVoiceChange(n) {
      if (this.bgGain) {
        this.bgGain.gain.value = n / 100;
        this.store.banzouVolume = n / 100;
      }
    },    
    doStop(goStart) {
      console.log(this.audioContext.state)
      this.playing = false;
      if(this.audioContext.state == "running") {
        this.audioContext.suspend();
        if(goStart) {
          this.curTime = 0;
          this.rTimer = null;
          this.remainTime = this.audioContext.currentTime;
        }
        // this.bgSource.stop();
        // this.voiceSource.stop();
      }
    },   
    jumpTo(n) {
      try {
        this.bgSource.stop();
      }catch(e){
      }
      const newBgSource = this.audioContext.createBufferSource();
      newBgSource.buffer = this.bgSource.buffer;  
      this.bgSource.disconnect();
      this.bgSource.buffer = null;
      newBgSource.connect(this.bgGain);  
      this.bgSource = newBgSource;
      this.bgSource.start(0, n);

      for(let index = 0; index < this.filelist.length; index++) {
        let item = this.filelist[index];
        try{
          item.voiceSource.stop();
        }catch(e){}
        const newVoiceSource = this.audioContext.createBufferSource();
        //console.log(item)
        console.log(item.voiceSource)
        newVoiceSource.buffer = item.voiceSource.buffer;  
        item.voiceSource.disconnect(); 
        item.voiceSource.buffer = null;
        newVoiceSource.connect(item.gainNode);  
        item.voiceSource = newVoiceSource;
        //newVoiceSource.start();
        this.startVoiceSource(n, item);        
      }
      // console.log("jumpTo-> ", n
      // this.voiceSource.stop();
      // const newVoiceSource = this.audioContext.createBufferSource();
      // newVoiceSource.buffer = this.voiceSource.buffer;  
      // this.voiceSource.disconnect();
      // newVoiceSource.connect(this.voiceGain);  
      // this.voiceSource = newVoiceSource;
      // //newVoiceSource.start();
      // this.startVoiceSource(n);
    },    

    // 
    startVoiceSource(n = 0, item) {

      if(item.delayPlayTimer){
        clearTimeout(item.delayPlayTimer);
        item.delayPlayTimer = null;
      }
      //  , source.start(, );  0 setTimeout
      let offsetTime = 0;
      offsetTime = n + item.delay;
      if(offsetTime >= 0) {
        item.voiceSource.start(0, offsetTime);
      }
      else {
        let n = offsetTime * -1000;
        offsetTime = 0;
        item.delayPlayTimer = setTimeout(()=>{
          item.voiceSource.start(0, offsetTime);
        }, n)        
      }

      // let delayTime = 0;
      // let offsetTime = 0;
      // // 
      // if(item.delay < 0) {
      //   delayTime = item.delay * -1; //
      // }
      // //  0.x 
      // else if(item.delay > 0) {
      //   offsetTime = n + item.delay; // 
      // }
      // console.log("------", delayTime, offsetTime)
      // item.voiceSource.start(delayTime, offsetTime);
    },
    onPlay() {
      this.doPlay();
    },
    doPlay() {
      console.log(this.audioContext.state)
      if(this.audioContext.state != "running") {
        this.playing = true;
        this.audioContext.resume();
        if(!this.rTimer) {
          this.setCurTime();
          this.jumpTo(0)
        }
        // 
        else {
          this.jumpTo(this.curTime);
        }
        // this.bgSource.start(n);
        // this.voiceSource.start(n);
      }
    },    
    onPause() {
      this.doStop();
    },
    onBack() {
      this.$router.replace("/");
    },    
    // ref  this.$refs["voice"+index] 
    setItemRef(el, index){
      return `voice${index}`;
    },
    onTimeUpdate() {
      if(!this.store.parsedLyrics || !this.store.parsedLyrics.length) {
        return;
      }
      const currentTime = this.curTime;
      // 
      for (let i = 0; i < this.store.parsedLyrics.length; i++) {
        if (
          currentTime >= this.store.parsedLyrics[i].time &&
          (i === this.store.parsedLyrics.length - 1 ||
            currentTime < this.store.parsedLyrics[i + 1].time)
        ) {
          this.curLrc = this.store.parsedLyrics[i].text; // 
          break;
        }
      }
    },

    //---------,,--------------
    getBg(item){
      let url = `background-image: url(${item.url}); background-repeat: no-repeat; background-position: center;`
      return url;
    },
    onImgFileSelect() {
      if(this.imgList.length >= 10){
        alert("10");
        return;
      }

      const fileList = this.$refs.imgFile.files;
      for(let file of fileList) {
        //  jpgpng
        console.log(file.type);
        if (file.type !== "image/png" && file.type !== "image/jpeg") {
          alert(" -> " + file.name);
          continue;
        }
        //  5M
        if (file.size > 5 * 1024 * 1024) {
          alert("5M -> " + file.name);
          continue;
        }
        let url = URL.createObjectURL(file);
        this.imgList.push({ url: url });
      }
      this.$refs.imgFile.value = "";
    },
    onImgAdd(){
      this.$refs.imgFile.click();
    },
    onRemoveImg(item, index) {
      this.imgList.splice(index, 1);
    },
    //----------------------
    isSurpportFile(file) {
      const regex = /\.(mp3|wav)$/i; // .mp3 .wav
      return regex.test(file.name);
    },
    async onAudioFileSelect(){
      this.store.showLoading();
      const file = this.$refs.audioFile.files[0];
      // console.log(file);
      if (!this.isSurpportFile(file)) {
        showToast(" wav, mp3 ");
        this.store.hideLoading();
        return;
      }
      this.doStop();
      let o = {
        reverb: false,
        reverbNode: new Pizzicato.Effects.Reverb({
          ...this.store.reverbOption
        }),
        volume: 100,
        delay: 0,
        file: file,
        //rMap: {},
        gainNode: null
      };
      this.filelist.push(o);
      await this.addVoice(o);
      this.$refs.audioFile.value = "";
      this.store.hideLoading();
    },
    onAudioAdd() {
      this.$refs.audioFile.click();
    },
    async addVoice(o) {
      const audBuffer = await this.loadAudioData(this.audioContext, o.file);
      let duration = audBuffer.duration;
      if(this.audioDuration < duration) {
        duration = this.audioDuration;
      }
      // 2. 
      const sampleRate = this.audioContext.sampleRate;
      const totalLength = duration * sampleRate;

      const voiceBuffer = this.audioContext.createBuffer(2, totalLength, sampleRate); // 

      // 3. 
      voiceBuffer.getChannelData(0).set(audBuffer.getChannelData(0).subarray(0, totalLength));
      let n = 0;
      if(audBuffer.numberOfChannels > 1){
        n = 1;
      }      
      voiceBuffer.getChannelData(1).set(audBuffer.getChannelData(n).subarray(0, totalLength));

      const voiceSource = this.audioContext.createBufferSource();
      voiceSource.buffer = voiceBuffer;
      o.voiceSource = voiceSource;
      let voiceGain = this.audioContext.createGain();
      voiceGain.gain.value = 1;  
      o.gainNode = voiceGain;

      voiceSource.connect(voiceGain);       
      // voiceSource.start();
      // let nObj = await util.connectReverbNode(voiceGain, o.reverb, this.audioContext);
      //let nObj = await util.connectReverbNode(voiceGain, this.store.reverbOption, this.audioContext);
      //let node = nObj.outputNode;
      //this.reverbNodes = nObj.reverbNodes;   
      //o.disconnect = nObj.disconnectReverb;
      voiceGain.connect(this.audioContext.destination);
    },

    //  
    async getReverb(num) {
      this.store.showLoading();
      const audioUrl = 'audio/reverb' + num + '.wav';
      
      try {
        const response = await fetch(audioUrl);
        if (!response.ok) {
          throw new Error('Network response was not ok ' + response.statusText);
        }
        
        const arrayBuffer = await response.arrayBuffer();
        
        //  AudioBuffer
        const audioBuffer = await this.audioContext.decodeAudioData(arrayBuffer);
        
        console.log(audioBuffer); //  AudioBuffer
        this.store.hideLoading();
        return audioBuffer; //  AudioBuffer
      } catch (error) {
        console.error('There has been a problem with your fetch operation:', error);
        this.store.hideLoading();
        return null;
      }
    },     

    //  ,, ,
    async onReverbChange(b, item, index) {
  
      if(b) {
        item.gainNode.disconnect();
        item.gainNode.connect(item.reverbNode);
        item.reverbNode.connect(this.audioContext.destination)
      }
      else {
        item.gainNode.disconnect();
        item.reverbNode.disconnect();
        item.gainNode.connect(this.audioContext.destination);
      }
    },

    // 
    onReverbDetailChange(opt){
      if(!this.curReverbItem) {
        return;
      }
      this.curReverbItem.reverbNode.time = opt.time;
      this.curReverbItem.reverbNode.decay = opt.decay;
      this.curReverbItem.reverbNode.mix = opt.mix;
    },    
    onVolumeChange(item, index) {
      console.log(item, index);
      item.gainNode.gain.value = item.volume / 100;
    },
    onDelayChange(item, index) {
      this.jumpTo(this.curTime);
    },
    onSetting() {
      this.settingShow = true;
    },
    onDurationChange(n) {
      this.remainTime = this.audioContext.currentTime - n;
      this.jumpTo(n);
    },
    onOrientChange(item, index) {
      console.log(item, index);
    },
    onVoiceRemove(item, index) {
      this.doStop();
      item.voiceSource.disconnect();
      item.gainNode.disconnect();
      if(item.disconnectReverb) {
        item.disconnectReverb();
        item.disconnectReverb = null;
      }
      // if(item.reverbNode) {
      //   item.reverbNode.disconnect();
      // }
      this.filelist.splice(index, 1);
    },
    compose() {
      if(!this.filelist.length){
        showToast("");
        return;
      }
      this.doStop();
      this.exportAudio(this.store.compress);
    },    
    async exportAudio(compress){
        //  OfflineAudioContext
        let length = this.audioDuration; 
        let audioCtx = new OfflineAudioContext({
            numberOfChannels: 2,
            length: 48000 * length,
            sampleRate: 48000,
        });
        let Pizzicato = createPizzicato(audioCtx);
        this.offlineAudioCtx = audioCtx;
        let bufferSource;
        //  bufferSource
        bufferSource = audioCtx.createBufferSource();
        bufferSource.buffer = this.bgSource.buffer;
    
        //  GainNode
        let gainNode = audioCtx.createGain();
        gainNode.gain.value = this.store.banzouVolume;
    
        //  Tone.js  OfflineAudioContext
        Tone.setContext(audioCtx);
    
        //  Tone.PitchShift 
        let pitchShift = new Tone.PitchShift();
        pitchShift.pitch = this.store.banzouPitch;  
        // 
        bufferSource.connect(gainNode);
        Tone.connect(gainNode, pitchShift);
        Tone.connect(pitchShift, audioCtx.destination);

        let vList = [];
        //filelist
        for(let i = 0; i < this.filelist.length; i++) {
          const item = this.filelist[i];
          //  
          let voiceBufferSource = audioCtx.createBufferSource();
          voiceBufferSource.buffer = item.voiceSource.buffer;

          let voiceGain = audioCtx.createGain();   
          let rItem = {
            reverb: item.reverb,
            reverbNode: new Pizzicato.Effects.Reverb({
              time: item.reverbNode.time,
              decay: item.reverbNode.decay,
              mix: item.reverbNode.mix
            }),
            // item.reverbNode,
            //rMap: {},
            gainNode: voiceGain,
            //reverbNode: null,
            voiceSource: voiceBufferSource,
            delay: item.delay,
            volume: item.volume,
          }
          console.log(rItem);
          voiceGain.gain.value = rItem.volume / 100;
          voiceBufferSource.connect(voiceGain);
          if(rItem.reverb) {
            voiceGain.connect(rItem.reverbNode);
            rItem.reverbNode.connect(audioCtx.destination);
          }
          else {
            voiceGain.connect(audioCtx.destination);
          }
          //let node = await this.connectReverbNode(rItem, voiceGain, audioCtx);
          //let nObj = await util.connectReverbNode(voiceGain, rItem.reverb, audioCtx);
          //let node = nObj.outputNode;
          //rItem.disconnect = nObj.disconnectReverb;          
          //node.connect(audioCtx.destination);    
          this.startVoiceSource(0, rItem);
          vList.push(voiceBufferSource)
        }
        
         bufferSource.start();

        console.log("start generate")
        //  OfflineAudioContext
        audioCtx.startRendering()
            .then(renderedBuffer => {
                if(!this.offlineAudioCtx){
                  return;
                }
                console.log('', renderedBuffer);
                if(compress){
                  showToast(",...");
                }
                downloadAudio(renderedBuffer, audioCtx.length, compress, (progress)=>{
                  // console.log("progress-mp3", progress);
                  if(!this.offlineAudioCtx){
                    return;
                  }                  
                  this.store.showProgess(progress);
                }, ()=>{
                  this.store.hideProgess();
                }); // 
            })
            .catch(err => {
                console.error(':', err);
            });
        showToast(",...");
        renderProgress.start(audioCtx, bufferSource.buffer.duration, (progess)=>{
          if(!this.offlineAudioCtx) {
            return;
          }
          console.log("progess", progess);
          this.store.showProgess(progess);
        }, ()=>{
          this.store.hideProgess();
          if(!onlyVoice) {
            bufferSource.disconnect();
            bufferSource.buffer = null;
          }
          for(let source of vList) {
            source.disconnect();
            source.buffer = null;
          }
        })

        // console.log(audioCtx.currentTime);
        // setTimeout(() => {
        //   console.log(audioCtx.currentTime, bufferSource);
        // }, 100);
        
    }     
  },
};
</script>
