<template>
  <van-dialog v-model:show="isVisible" title="" @close="onCloseDialog">
    
    <div class="c-setting-box">
      <van-cell title=":" :value="option.value.time" />
      <div class="c-setting-sliderbox">
        <van-slider
          v-model="option.value.time"
          @change="onTimeChange"
          min="0"
          max="5"
          step="0.1"
        />
      </div>
      <van-cell title="" :value="option.value.decay" />
      <div class="c-setting-sliderbox">
        <van-slider
          v-model="option.value.decay"
          @change="onDecayChange"
          min="0"
          max="5"
          step="0.1"
        />
      </div>      
      <van-cell title="" :value="option.value.mix" />
      <div class="c-setting-sliderbox">
        <van-slider
          v-model="option.value.mix"
          @change="onMixChange"
          min="0"
          max="1"
          step="0.01"
        />
      </div>
      <div class="c-setting-resetbtnbox">
        <van-button type="warning" size="small" @click="onReset"></van-button>
      </div>      
    </div>
  </van-dialog>    
</template>

<script>
import useStore from "../store";
import { ref } from "vue";
export default {
  name: "setting",
  props: {
    // option: { type: Object, default: () => ({ time: 0, decay: 0, mix: 0 }) }
  },
  emits: ['change'],  
  setup(props, { emit }) {
   
    //  
    // let onCloseDialog = ()=> {  
    //   console.log('');
    //   emit('update:visible', false);  
    // } 
    const store = useStore();
    let isVisible = ref(false);
    let option = ref({});
    return { isVisible, option, store };
  },
  methods: {
    show(opt) {
      this.isVisible = true;
      this.option.value = opt;
    },
    onCloseDialog() {
      this.isVisible = false;
      this.option.value = {};
      this.$emit('close');
    },
    onTimeChange(val) {
      this.option.value.time = val;
      this.$emit('change', this.option.value);
    },
    onDecayChange(val) {
      this.option.value.decay = val;
      this.$emit('change', this.option.value);
    },
    onMixChange(val) {
      this.option.value.mix = val;
      this.$emit('change', this.option.value);
    },
    onReset() {
      this.option.value.time = this.store.reverbOption_default.time;
      this.option.value.decay = this.store.reverbOption_default.decay;
      this.option.value.mix = this.store.reverbOption_default.mix;
      this.$emit('change', this.option.value);
    }
  }
};
</script>

