// 
let working = false;
async function downloadAudio(abuffer, len, compress = false, onProgress, onFinish) {
    // // set sample length and rate
    // var duration = abuffer.duration,
    //   rate = abuffer.sampleRate,
    //   offset = 0;
    // Generate audio file and assign URL
    working = true;
    var audio = await bufferToWave(abuffer, len, compress, onProgress, onFinish);
    if(!audio) {
      return;
    }

    var new_file = URL.createObjectURL(audio);
    var fileName = 'song' + (compress ? '.mp3' : '.wav');
    var link = document.createElement('a');
    link.href = new_file;
    link.download = fileName;
    link.click();
    window.URL.revokeObjectURL(link.href);
    onFinish();
}

function stopDownload(){
  working = false;
}

async function bufferToWave(abuffer, len, compress, onProgress) {
  var numOfChan = abuffer.numberOfChannels,
    length = len * numOfChan * 2 + 44,
    buffer = new ArrayBuffer(length),
    view = new DataView(buffer),
    channels = [],
    i,
    sample,
    offset = 0,
    pos = 0;

  // write WAVE header
  setUint32(0x46464952); // "RIFF"
  setUint32(length - 8); // file length - 8
  setUint32(0x45564157); // "WAVE"

  setUint32(0x20746d66); // "fmt " chunk
  setUint32(16); // length = 16
  setUint16(1); // PCM (uncompressed)
  setUint16(numOfChan);
  setUint32(abuffer.sampleRate);
  setUint32(abuffer.sampleRate * 2 * numOfChan); // avg. bytes/sec
  setUint16(numOfChan * 2); // block-align
  setUint16(16); // 16-bit (hardcoded in this demo)

  setUint32(0x61746164); // "data" - chunk
  setUint32(length - pos - 4); // chunk length

  // write interleaved data
  for (i = 0; i < abuffer.numberOfChannels; i++)
    channels.push(abuffer.getChannelData(i));

  while (pos < length) {
    for (i = 0; i < numOfChan; i++) {
      // interleave channels
      sample = Math.max(-1, Math.min(1, channels[i][offset])); // clamp
      sample = (0.5 + sample < 0 ? sample * 32768 : sample * 32767) | 0; // scale to 16-bit signed int
      view.setInt16(pos, sample, true); // write 16-bit sample
      pos += 2;
    }
    offset++; // next source sample
  }

  //wav  
  if(compress) {
    let wavHdr = lamejs.WavHeader.readHeader(new DataView(buffer));
    let wavSamples = new Int16Array(
      buffer,
      wavHdr.dataOffset,
      wavHdr.dataLen / 2,
    );
    return await wavToMp3(wavHdr.channels, wavHdr.sampleRate, wavSamples, onProgress);
  }

  // create Blob
  return new Blob([buffer], { type: 'audio/wav' });

  function setUint16(data) {
    view.setUint16(pos, data, true);
    pos += 2;
  }

  function setUint32(data) {
    view.setUint32(pos, data, true);
    pos += 4;
  }
}

async function wavToMp3(channels=2, sampleRate=48000, samples, onProgress) {
  var buffer = [];
  /*
    !!!!!: ,,
    //- 44100   offlineContext2 offline1 
    //offline2 44100 2   
    //  ...
  */
  var mp3enc = new lamejs.Mp3Encoder(channels, sampleRate, 128);
  var remaining = samples.length;
  var samplesPerFrame = 4608; // 1152 4608  
  console.time();
  for (var i = 0; remaining >= samplesPerFrame; i += samplesPerFrame) {
    var mono = samples.subarray(i, i + samplesPerFrame);
    var left = mono.filter(function (element, index, array) {
      return index % 2 === 0;
    });
    var right = mono.filter(function (element, index, array) {
      return index % 2;
    });
    //    :    mono
    /*
      20211202. , , mp3enc.encodeBuffer,,
                ,,,  ,
                ,. 
    */
    // var mp3buf = mp3enc.encodeBuffer(left, right);
    if(!working) {
      return null;
    }
    var mp3buf = await encodeMp3(mp3enc,left, right);
    
    if (mp3buf && mp3buf.length > 0) {
      buffer.push(new Int8Array(mp3buf));
    }
    remaining -= samplesPerFrame;
    //console.log(i, remaining, samples.length)
    onProgress(Math.floor(i/samples.length * 100))
  }
  var d = mp3enc.flush();
  if (d.length > 0) {
    buffer.push(new Int8Array(d));
  }
  console.timeEnd();
  var mp3Blob = new Blob(buffer, { type: 'audio/mp3' });
  return mp3Blob;
  //var bUrl = window.URL.createObjectURL(mp3Blob);

  // send the download link to the console
  //console.log('mp3 download:', bUrl);
}

async function encodeMp3(mp3enc, left, right) {
  return new Promise((rs, rj)=>{
    let r = mp3enc.encodeBuffer(left, right);
    setTimeout(()=>{
      rs(r);
    }, 0)
  })
}

export default downloadAudio;
export {stopDownload};
