    async init(recordedSegments, accompanimentBlob, recordDuration) {
      this.store.showLoading();
      //this.reverb = await this.getReverb();
      // 1. 
      const accompanimentBuffer = await this.loadAudioData(this.audioContext,accompanimentBlob);

      // 2. 
      const sampleRate = this.audioContext.sampleRate;
      const totalLength = recordDuration * sampleRate;

      const bgBuffer = this.audioContext.createBuffer(2, totalLength, sampleRate); // 

      // 3. 
      bgBuffer.getChannelData(0).set(accompanimentBuffer.getChannelData(0).subarray(0, totalLength));
      let n = 0;
      if(accompanimentBuffer.numberOfChannels > 1){
        n = 1;
      }      
      bgBuffer.getChannelData(1).set(accompanimentBuffer.getChannelData(n).subarray(0, totalLength));
      
      const bgSource = this.audioContext.createBufferSource();
      bgSource.buffer = bgBuffer;

      this.bgGain = this.audioContext.createGain();
      this.accompanyVal = parseInt(this.store.banzouVolume * 100);
      this.bgGain.gain.value = this.store.banzouVolume;
      // this.accompanyVal = Math.round(this.bgGain.gain.value * 100);
      bgSource.connect(this.bgGain);  
      Tone.setContext(this.audioContext);
      var pitchShift = new Tone.PitchShift();
      pitchShift.pitch = this.store.banzouPitch;  
      Tone.connect(this.bgGain, pitchShift);    
      // this.bgGain.connect(this.audioContext.destination);
      Tone.connect(pitchShift, this.audioContext.destination);

      // 4.  start 
      // recordedSegments.sort((a, b) => a.start - b.start); //  

      for (let i = recordedSegments.length - 1; i >= 1; --i) {
        const segment = recordedSegments[i];
        const preSeg = recordedSegments[i - 1];
        let r = segment.start - preSeg.end
        if (r < 0) {
          preSeg.end = segment.start;
        }
        if (r > 0) {
          let silentBuffer = this.createSilentAudio(this.audioContext, r);
          recordedSegments.splice(i, 0, {
            start: preSeg.end,
            end: segment.start,
            buffer: silentBuffer
          })
        }

      }

      const voiceBuffer = this.audioContext.createBuffer(2, totalLength, sampleRate); // 

      // 5. 
      for (let i = 0; i < recordedSegments.length; i++) {
        let segment = recordedSegments[i];
        let audioBuffer;
        // ,,buffer, 
        if(segment.buffer) {
          audioBuffer = segment.buffer;
        }
        else {
          audioBuffer = await this.loadAudioData(this.audioContext, segment.blob);
        }
        const startSample = Math.floor(segment.start * sampleRate);

        // 
        const endSample = Math.min(startSample + audioBuffer.length, totalLength);

        // 
        voiceBuffer.getChannelData(0).set(audioBuffer.getChannelData(0).slice(0, Math.floor(endSample - startSample)), startSample);
        let n = 0;
        console.log("channel " + audioBuffer.numberOfChannels)
        if(audioBuffer.numberOfChannels > 1){
          n = 1;
        }
        voiceBuffer.getChannelData(1).set(audioBuffer.getChannelData(n).slice(0, Math.floor(endSample - startSample)), startSample);
      }

      // 6. 
      const voiceSource = this.audioContext.createBufferSource();
      voiceSource.buffer = voiceBuffer;
      this.voiceGain = this.audioContext.createGain();
      this.humanVal = parseInt(this.store.humanVolume);
      this.voiceGain.gain.value = this.store.humanVolume;

      //this.humanVal = Math.round(this.voiceGain.gain.value * 100);
      voiceSource.connect(this.voiceGain); 
      //  
      //let node = await util.connectReverbNode(this.voiceGain, this.store.reverb, this.audioContext);
      // let nObj = await util.connectReverbNode(this.voiceGain, this.store.reverb, this.audioContext);
      // let node = nObj.outputNode;
      // this.disconnectReverb = nObj.disconnectReverb;      
      // this.reverbNodes = nObj.reverbNodes; 
      this.curReverbNode = new Pizzicato.Effects.Reverb({
        ...this.store.reverbOption
      })
      
      this.onReverbChange(this.store.reverb);
      
      this.audioContext.onstatechange = () => {
        console.log("state-> ", this.audioContext.state);
      };
      console.log("delay time->", this.audioContext.currentTime)
      this.bgSource = bgSource;
      this.voiceSource = voiceSource;
      this.playing = true;

      this.remainTime = this.audioContext.currentTime;

      this.setCurTime();

      // 
      bgSource.start();
      // voiceSource.start();
      this.startVoiceSource(); // 
      console.log("voiceSource", voiceSource);
      this.store.hideLoading();
      
      // 7.  MP3 
      //this.exportBufferAsMp3(combinedBuffer);
    },
