    doStop(goStart) {
      console.log(this.audioContext.state)
      this.playing = false;
      if(this.audioContext.state == "running") {
        this.audioContext.suspend();
        if(goStart) {
          this.curTime = 0;
          this.rTimer = null;
          this.remainTime = this.audioContext.currentTime;
        }
        // this.bgSource.stop();
        // this.voiceSource.stop();
      }
    },
    // , node, start
    jumpTo(n) {

      this.bgSource.stop();
      const newBgSource = this.audioContext.createBufferSource();
      newBgSource.buffer = this.bgSource.buffer;  
      this.bgSource.disconnect();
      this.bgSource.buffer = null;
      newBgSource.connect(this.bgGain);  
      this.bgSource = newBgSource;
      console.log("bg", 0, n)
      this.bgSource.start(0, n);

      this.voiceSource.stop();
      const newVoiceSource = this.audioContext.createBufferSource();
      newVoiceSource.buffer = this.voiceSource.buffer;  
      this.voiceSource.disconnect();
      this.voiceSource.buffer = null;
      newVoiceSource.connect(this.voiceGain);  
      this.voiceSource = newVoiceSource;
      //newVoiceSource.start();
      this.startVoiceSource(n);
    },

    startVoiceSource(n = 0, source=this.voiceSource) {
      if(this.delayPlayTimer){
        clearTimeout(this.delayPlayTimer);
        this.delayPlayTimer = null;
      }
      //  , source.start(, );  0 setTimeout
      let offsetTime = 0;
      offsetTime = n + this.store.voiceDelay;
      if(offsetTime >= 0) {
        source.start(0, offsetTime);
      }
      else {
        let n = offsetTime * -1000;
        offsetTime = 0;
        this.delayPlayTimer = setTimeout(()=>{
          source.start(0, offsetTime);
        }, n)        
      }
      // let delayTime = 0;
      // let offsetTime = 0;
      // /**
      //  * source.start(0, offsetTime);  ....
      //  */
      // // 
      // if(this.store.voiceDelay >= 0) {
      //   offsetTime = n + this.store.voiceDelay; // 
      //   source.start(0, offsetTime);
      // }
      // //  0.x 
      // else {
      //   delayTime = this.store.voiceDelay * -1; //
      //   offsetTime = n;// 
      //   this.delayPlayTimer = setTimeout(()=>{
      //     source.start(0, offsetTime);
      //   }, delayTime*1000)
      // }
      
    },

    doPlay() {
      console.log(this.audioContext.state)
      if(this.audioContext.state != "running") {
        this.playing = true;
        this.audioContext.resume();
        if(!this.rTimer) {
          this.setCurTime();
          this.jumpTo(0)
        }
        // this.bgSource.start(n);
        // this.voiceSource.start(n);
      }
    },

    // record, recordaudioupdate,audio
    setCurTime() {
      let n = this.audioContext.currentTime - this.remainTime;
      if(n >= this.store.recordLength){
        this.doStop(true);
        return;
      }
      if(!this.dragging) {
        this.curTime = n;
      }
      this.setCurLrc();
      //console.log("curTime-> ", this.curTime,  this.store.banzouLength);
      this.rTimer = requestAnimationFrame(this.setCurTime);
    },

    // compose 
    setCurLrc() {
      if(!this.store.parsedLyrics || !this.store.parsedLyrics.length) {
        return;
      }
      const currentTime = this.curTime;
      // 
      for (let i = 0; i < this.store.parsedLyrics.length; i++) {
        if (
          currentTime >= this.store.parsedLyrics[i].time &&
          (i === this.store.parsedLyrics.length - 1 ||
            currentTime < this.store.parsedLyrics[i + 1].time)
        ) {
          this.curLrc = this.store.parsedLyrics[i].text; // 
          break;
        }
      } 
    },

    // 
    createSilentAudio(audioCtx, duration) {
      const channels = 2;
      const frameCount = audioCtx.sampleRate * duration;
      const silentBuffer = audioCtx.createBuffer(channels, frameCount, audioCtx.sampleRate);
      return silentBuffer;
    },

    // 
    loadAudioData(audioContext, blob) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => {
          audioContext
            .decodeAudioData(reader.result)
            .then((audioBuffer) => resolve(audioBuffer))
            .catch((err) => reject(err));
        };
        reader.onerror = () => reject(reader.error);
        reader.readAsArrayBuffer(blob);
      });
    },

    // exportBufferAsMp3(audioBuffer) {
    //   console.log("exportBufferAsMp3", audioBuffer);
    //   // const wav = audioBufferToWav(audioBuffer); //  audioBufferToWav 
    //   // const mp3Blob = wavToMp3(wav); //  wavToMp3 

    //   // const url = URL.createObjectURL(mp3Blob);
