    async exportAudio(onlyVoice, compress){
        //  OfflineAudioContext
        let length = this.store.recordLength; // 10
        let audioCtx = new OfflineAudioContext({
            numberOfChannels: 2,
            length: 48000 * length,
            sampleRate: 48000,
        });
        let Pizzicato = createPizzicato(audioCtx);
        this.offlineAudioCtx = audioCtx;
        let bufferSource;
        if(!onlyVoice) {
          //  bufferSource
          bufferSource = audioCtx.createBufferSource();
          bufferSource.buffer = this.bgSource.buffer;
      
          //  GainNode
          let gainNode = audioCtx.createGain();
          gainNode.gain.value = this.store.banzouVolume;

          //  Tone.js  OfflineAudioContext
          Tone.setContext(audioCtx);
      
          //  Tone.PitchShift 
          let pitchShift = new Tone.PitchShift();
          pitchShift.pitch = this.store.banzouPitch;  
      
          // 
          bufferSource.connect(gainNode);
          Tone.connect(gainNode, pitchShift);
          Tone.connect(pitchShift, audioCtx.destination);
        }
    
        //  
        let voiceBufferSource = audioCtx.createBufferSource();
        voiceBufferSource.buffer = this.voiceSource.buffer;
        let voiceGain = audioCtx.createGain();
        voiceGain.gain.value = this.store.humanVolume;

        //this.humanVal = Math.round(this.voiceGain.gain.value * 100);
        voiceBufferSource.connect(voiceGain);     
        //  
        //let node = await util.connectReverbNode(voiceGain,this.store.reverb, audioCtx);
        // let nObj = await util.connectReverbNode(voiceGain, this.store.reverb, audioCtx);
        // let node = nObj.outputNode;
        // node.connect(audioCtx.destination);    
        if(this.store.reverb) {
            let node = new Pizzicato.Effects.Reverb({
              time: this.curReverbNode.time,
              decay: this.curReverbNode.decay,
              mix: this.curReverbNode.mix
            })
            voiceGain.connect(node);
            node.connect(audioCtx.destination);
        }
        else {
          voiceGain.connect(audioCtx.destination);
        }
        if(!onlyVoice) {
          bufferSource.start();
        }
        this.startVoiceSource(0, voiceBufferSource);
        console.log("start generate")
        //  OfflineAudioContext
        audioCtx.startRendering()
            .then(renderedBuffer => {
                if(!this.offlineAudioCtx){
                  return;
                }
                console.log('', renderedBuffer);
                if(compress){
                  showToast(",...");
                }
                downloadAudio(renderedBuffer, audioCtx.length, compress, (progress)=>{
                  // console.log("progress-mp3", progress);
                  if(!this.offlineAudioCtx){
                    return;
                  }                  
                  this.store.showProgess(progress);
                }, ()=>{
                  this.store.hideProgess();
                }); // 
            })
            .catch(err => {
                console.error(':', err);
            });
        showToast(",...");
        renderProgress.start(audioCtx, this.bgSource.buffer.duration, (progess)=>{
          if(!this.offlineAudioCtx) {
            return;
          }
          console.log("progess", progess);
          this.store.showProgess(progess);
        }, ()=>{
          this.store.hideProgess();
          if(!onlyVoice) {
            bufferSource.disconnect();
            bufferSource.buffer = null;
          }
          voiceBufferSource.disconnect();
          voiceBufferSource.buffer = null;
        })
    }    
  },
};
</script>
<style scoped>
.c-reverb-gp {
  display: flex;
  align-items: center;
}
</style>
