let renderProgress = {
  timer: null,
  start(audioContext, total, onProgess, finish) {
    this.timer = setInterval(() => {
      if (audioContext.currentTime < total) {
        onProgess(Math.floor(audioContext.currentTime/total * 100));
      }
      else {
        onProgess(100);
        this.stop();
        setTimeout(() => {
          finish();
        }, 1000);
      }
    }, 200);
  },
  stop() {
    clearInterval(this.timer);
  }
};

export {renderProgress}
