<template>
  <div class="container record">
    <van-nav-bar
      :title="musicName"
      left-text=""
      @click-left="onBack()"
      right-text=""
      @click-right="onSetting"
    />
    <audio
      ref="audio"
      @timeupdate="onTimeUpdate"
      style="display: none"
      @ended="finish"
      @loadedmetadata="onLoadedMetadata"
    ></audio>
    <div class="content c-text-center">
      <div class="c-lrcbox">
        <van-picker
          @touchstart="onTouchStart"
          @touchend="onTouchEnd"
          :class="getCurClass()"
          :show-toolbar="false"
          :columns="store.parsedLyrics"
          @change="onChange"
          v-model="curVal"
          :readonly="countingdown || !playing"
          visible-option-num="8"
          option-height="8vh"
          swipe-duration="400"
        />
        <div class="c-cd-dotbox" v-show="countingdown">
          <i v-for="n in dotCount" :key="n" class="c-cd-dotitem"></i>
        </div>
      </div>
      <div class="c-ctrlbox">
        <div class="c-ctrlitem" @click="onReplay">
          <van-icon name="revoke" class="c-ctrlicon" />
          <span></span>
        </div>
        <div class="c-ctrlitem">
          <van-icon
            v-show="playing"
            name="pause-circle-o"
            class="c-ctrlicon-big"
            @click="onPause"
          />
          <van-icon
            name="play-circle"
            class="c-ctrlicon-big"
            v-show="!playing"
            @click="onPlay"
          />
        </div>
        <div class="c-ctrlitem" @click="onFinish">
          <van-icon name="passed" class="c-ctrlicon" />
          <span></span>
        </div>
      </div>
      <div class="c-timebox">
        {{ util.convertTime(curTime) }} / {{ util.convertTime(store.banzouLength) }}
      </div>
      <div>
        <van-button class="c-jump-btn" plain type="default" v-show="jumpShow" @click="jumpToSing"></van-button>
      </div>
        <canvas ref="waveBox" width="375" height="40" class="c-re-wave"></canvas>
    </div>
  </div>
  <van-action-sheet
    v-model:show="fShow"
    :actions="fShowList"
    @select="onFshowSelect"
    cancel-text=""
    close-on-click-action
    @cancel="onFshowCancel"
  />
  <SettingDialog v-model:visible="settingShow"/>
  <!-- <van-dialog v-model:show="settingShow" title="">
    <div class="c-setting-box">
      <van-cell title="" :value="store.lrcDelayTime" />
      <div class="c-setting-sliderbox">
        <van-slider
          v-model="store.lrcDelayTime"
          @change="onlrcDelayChange"
          min="-2"
          max="2"
          step="0.1"
        />
      </div>
    </div>
  </van-dialog> -->
</template>
<script>
// @ is an alias to /src

import useStore from "../store";
import { showConfirmDialog } from "vant";
import SettingDialog from "../components/SettingDialog";
import util from "../common/js/util.js";

/* eslint-disable*/
// import { showToast } from "vant";
export default {
  name: "RecordView",
  components: {SettingDialog},
  setup() {
    // store,
    const store = useStore();
    return { store, util };
  },
  data() {
    return {
      // 
      musicName: "",
      // 
      curTime: 0,
      // 
      jumpShow: false,
      // store
      // store: null,
      // url
      audioUrl: null,
      curVal: [], // 
      // 
      countingdown: false,
      // 
      dotCount: 5,
      // 
      needCountdown: false,
      // 
      showCountdownTime: null,
      // 
      scrolling: false,
      // 
      playing: false,
      //  
      fShow: false,
      fShowList: [
        { name: "" },
        { name: "" },
      ],
      // 
      mediaRecorder: null,
      // 
      // blankAudioBuffer: null,
      recordStarttime: 0,
      // 
      settingShow: false,

      //  ,
      audioCtx: null,
      // 
      pitchShift_banzou: null,
      // 
      gainNode_banzou: null,

      // 
      analyser: null,
      // 
      ctx: null,

    };
  },
  mounted() {
    document.addEventListener('visibilitychange', this.setVisibilyChange); 
    // store
   console.log(this.store);
    // console.log(store.banzou);
    if (!this.store.banzou) {
      alert("");
      this.$router.replace("/");
      return;
    }    
    navigator.mediaDevices.getUserMedia({ audio: {
      channelCount: 2,
      sampleRate: 48000, //
      sampleSize: 16, //  16 
      // true
      echoCancellation: false,  // 
      noiseSuppression: false, // 
      autoGainControl: true, // 
    } })
    .then(stream => {
      this.initMediaRecorder(stream);

      // 
      this.musicName = this.store.banzou.name.replace(/\.(mp3|wav)$/i, "");
      // 
      this.store.audioChunk = [];
      this.connectAuido();
      this.initAnalyser(stream);
      // 
      if (this.store.lrc) {
        this.store.readLrcText().then(() => {
          // console.log(lrcText);
          // this.columns = this.parseLRC(lrcText);
          //this.lrcText = lrcText;
          //this.parseLRC(this.lrcText);
          if (this.store.parsedLyrics[0]) {
            this.setCountdownTime(this.store.parsedLyrics[0].value);
          }          
          this.loadAudio();
          // this.mediaRecorder.start();
        });
      } else {
        this.loadAudio();
        // this.mediaRecorder.start();
      }      
    }).catch(err => {
      console.log(err);
      alert(",");
    });
 
  },
  unmounted() {
    document.removeEventListener('visibilitychange', this.setVisibilyChange); 
    if (this.audioUrl) {
      URL.revokeObjectURL(this.audioUrl);
      this.audioUrl = null;
    }    
    if (this.$refs.audio) {
      this.$refs.audio.pause();
      this.$refs.audio.src = "";
    }
    if(this.mediaRecorder){
      this.mediaRecorder.stop();
      this.mediaRecorder = null;
    }
    this.clearTimer();
    // this.audioCtx = null;
    // Tone.setContext(null);
  },
  watch: {
    // 
    "store.banzouVolume": {
      handler() {
        if (this.audioCtx) {
          this.gainNode_banzou.gain.value = this.store.banzouVolume;
          //this.gainNode_banzou.gain.setValueAtTime(0.5, this.audioCtx.currentTime);
        }
      },
    },
    //
    "store.banzouPitch": {
      handler() {
        if (this.audioCtx) {
          this.pitchShift_banzou.pitch = this.store.banzouPitch;
          // this.pitchShift_banzou.set({ pitch: 2 });
        }
      },
    },    
  },
  methods: {
    test1() {
      console.log("test1" + this.store);
    },
    setVisibilyChange() {
      //  audioContextresume,...
      if (document.visibilityState === 'visible') {
        try {
          this.audioCtx.suspend();
        }
        catch(e){}
      }
      else if (document.visibilityState === 'hidden') {
        this.onPause();
      }
    },     
    //,
    initAnalyser(stream) {
        const source = this.audioCtx.createMediaStreamSource(stream);
        //  AnalyserNode 
        this.analyser = this.audioCtx.createAnalyser();
        // fftSize  FFT  32  32768  2  326412825651210242048 
        // fftSize 
        this.analyser.fftSize = 2048; //  FFT   2048
        source.connect(this.analyser);
        let cvs = this.$refs.waveBox;
        this.ctx = cvs.getContext("2d");
        this.drawWaveform();
    },
    // 
    drawWaveform() {
        const bufferLength = this.analyser.fftSize;
        const dataArray = new Uint8Array(bufferLength);
        let canvas = this.$refs.waveBox;
        let canvasCtx = this.ctx;
        let draw = ()=> {
            // 
            this.analyser.getByteTimeDomainData(dataArray);

            // 
            canvasCtx.clearRect(0, 0, canvas.width, canvas.height);

            // 
            canvasCtx.lineWidth = 1.5;
            canvasCtx.strokeStyle = '#e4310d';

            // 
            canvasCtx.beginPath();
            const sliceWidth = canvas.width * 1.0 / bufferLength;
            let x = 0;

            for (let i = 0; i < bufferLength; i++) {
                const v = dataArray[i] / 128.0;
                const y = v * canvas.height / 2;

                if (i === 0) {
                    canvasCtx.moveTo(x, y);
                } else {
                    canvasCtx.lineTo(x, y);
                }

                x += sliceWidth;
            }

            canvasCtx.lineTo(canvas.width, canvas.height / 2);
            canvasCtx.stroke();

            // 
            this.animationId = requestAnimationFrame(draw);
        }

        draw(); // 
    },    
    //  
    checkQianzouLength() {
      if(!this.store.parsedLyrics || !this.store.parsedLyrics.length) {
        return;
      }
      if(this.store.parsedLyrics[0].time > 10) {
        this.jumpShow = true;
      }
    },
    jumpToSing() {
      let time = this.store.parsedLyrics[0].value;
      this.curVal = [time];
      this.onChange({ selectedValues: [time]});
      this.changeTime();
      this.jumpShow = false;
    },
    clearTimer() {
      clearTimeout(this.cdTimer);
      clearTimeout(this.delayTimer);
      clearTimeout(this.touchEndTimer);
      cancelAnimationFrame(this.animationId);
    },
    onSetting() {
      this.settingShow = true;
    },
    //
    // onlrcDelayChange(v) {
    //   this.parseLRC(this.lrcText);
    // },
    setMediaRecorder() {
      this.mediaRecorder.ondataavailable = event => {
        console.log(event.data)
        if(!this.$refs.audio) {
          return;
        }
        this.store.audioChunk.push({
          start: this.recordStarttime,
          end: this.$refs.audio.currentTime,
          blob: event.data
        });
        console.log(this.store.audioChunk)
      };
    },
    initMediaRecorder(stream) {
      this.mediaRecorder = new MediaRecorder(stream);
      this.setMediaRecorder();
      // console.log(this.mediaRecorder.resume);
      // mediaRecorder.start();
      // this.recordChunks = [];


      // this.mediaRecorder.onstop = () => {
        // const blob = new Blob(this.recordChunks, { 'type': 'audio/ogg; codecs=opus' });
        // // 
        // const voiceUrl = URL.createObjectURL(blob);
        // const a = document.createElement('a');
        // a.href = voiceUrl;
        // a.download = 'vocal.ogg';
        // document.body.appendChild(a);
        // a.click();
      // };

      // // 
      // setTimeout(() => {
      //   mediaRecorder.stop();
      // }, 15000); // 5
    },
    removeRecordedSegmentsAfter(time) {
      // 
      this.store.audioChunk = this.store.audioChunk.filter(segment => segment.start < time);
    },  
    // 
    createSilentAudio(duration) {
      // 
      const audioCtx =  this.audioCtx; //this.audioContext || (this.audioContext = new AudioContext());
      const channels = 2;
      const frameCount = audioCtx.sampleRate * duration;
      const silentBuffer = audioCtx.createBuffer(channels, frameCount, audioCtx.sampleRate);
      return silentBuffer;
    },      
    //   ,
    getCurClass(){
      let sText = [];
      if(this.dotCount <= 0 ) {
        sText.push('c-start-sing')
      }
      if(this.scrolling){
        sText.push('c-picker-scrolling')
      }
      return sText.join(' ');
    },
    // 
    onTouchStart(){
      if (this.countingdown || !this.playing) {
        return;
      }
      console.log("onTouchStart");
      if(this.touchEndTimer) {
        clearTimeout(this.touchEndTimer);
        this.touchEndTimer = null;
      }
      else {
        // touchEndTimer  
        this.touchTime = this.$refs.audio.currentTime;
      }
      this.scrolling = true;
    },
    // 
    onTouchEnd(){
      if (this.countingdown || !this.playing) {
        return;
      }
      console.log("will onTouchEnd");
      this.touchEndTimer = setTimeout(() => {
        console.log("onTouchEnd");
        this.scrolling = false;
        this.touchEndTimer = null;
        this.changeTime();
        // todo onchange
      }, 500);
    },
    // setRecordAudio(touchTime, nextTime) {
    //   if(nextTime > touchTime) {
    //     let buffer = this.createSilentAudio(nextTime - touchTime);
    //     this.store.audioChunk.push({
    //       end: nextTime,
    //       blob: buffer.getChannelData(0)
    //     });
    //     console.log("chunk ", this.store.audioChunk);
    //   }
    // },
    // 
    loadAudio() {
      this.audioUrl = window.URL.createObjectURL(this.store.banzou);
      this.$refs.audio.src = this.audioUrl;
    },
    onLoadedMetadata() {
      this.store.banzouLength = this.$refs.audio.duration;
      this.mediaRecorder.start();
      this.$refs.audio.play();
      this.playing = true;
      this.checkQianzouLength();
    },

    // connect audio
    connectAuido() {
      var audioCtx = new (window.AudioContext || window.webkitAudioContext);
      var mediaElem = this.$refs.audio;
      var stream = audioCtx.createMediaElementSource(mediaElem);
      var gainNode = audioCtx.createGain();

      // This a normal connection between to native AudioNodes.
      stream.connect(gainNode);

      // Set the context used by Tone.js
      Tone.setContext(audioCtx);
      gainNode.gain.value = this.store.banzouVolume;

      var pitchShift = new Tone.PitchShift();
      pitchShift.pitch = this.store.banzouPitch;
      // Use the Tone.connect() helper to connect native AudioNodes with the nodes provided by Tone.js
      Tone.connect(gainNode, pitchShift);
      Tone.connect(pitchShift, audioCtx.destination);

      this.audioCtx = audioCtx;
      this.gainNode_banzou = gainNode;
      // console.log(11111,Tone)
      this.pitchShift_banzou = pitchShift;
    },

    // -- store  
    // readText() {
    //   return new Promise((rs) => {
    //     let fr = new FileReader();
    //     fr.onload = (e) => {
    //       rs(e.target.result);
    //     };
    //     fr.readAsText(this.store.lrc, "GBK");
    //   });
    // },
    
    // 
    onBack() {
      this.$router.replace("/");
    },
    // // js
    // parseLRC(lrcContent) {
    //   const lines = lrcContent.split(/[\r\n]/);
    //   const timeExp = /\[(\d{2}):(\d{2})\.(\d{2,3})\]/; //  [mm:ss.xx]
    //   const lyricsList = [];

    //   lines.forEach((line) => {
    //     const match = timeExp.exec(line);
    //     if (match) {
    //       const minutes = parseInt(match[1], 10);
    //       const seconds = parseInt(match[2], 10);
    //       let milliseconds = parseInt(match[3], 10);
    //       milliseconds = match[3].length > 2 ? parseInt(match[3], 10) / 1000 : parseInt(match[3], 10) / 100;
    //       const timeInSeconds = minutes * 60 + seconds + milliseconds;

    //       const lyricText = line.replace(timeExp, "").trim();

    //       // Picker
    //       lyricsList.push({
    //         text: lyricText,
    //         value: timeInSeconds + this.store.lrcDelayTime,
    //         time: timeInSeconds + this.store.lrcDelayTime,
    //       });
    //     }
    //   });

    //   this.store.parsedLyrics = lyricsList;
    // },
    
    // 
    setCountdownTime(n) {
      if (n < 5) {
        this.showCountdownTime = 0;
      } else {
        this.showCountdownTime = n - 5;
      }
      this.needCountdown = true;
    },
    // 
    onPause() {
      if(this.countingdown){
        return;
      }
      this.$refs.audio.pause();
      this.mediaRecorder.pause();
      this.playing = false;
    },
    // 
    onPlay(){
      if(this.countingdown){
        return;
      }      
      this.$refs.audio.play();
      this.mediaRecorder.resume();
      this.playing = true;
    },
    // 
    onTimeUpdate() {
      const audio = this.$refs.audio;
      if (!audio) {
        return;
      }
      this.curTime = audio.currentTime;
      
      if (this.countingdown || this.scrolling) {
        return;
      }
      console.log("---------onTimeUpdate");

      const currentTime = audio.currentTime;
     // console.log(currentTime);
     // , (,,)
      if (this.needCountdown && currentTime >= this.showCountdownTime) {
        this.jumpShow = false;
        let n = currentTime - this.showCountdownTime; // , 0.3
        let m = Math.floor(n); // 0 
        let delay = 1000 - 1000 * (n - m);  //  300 1000, 
        this.dotCount = Math.floor(5 - (n - m)); // 5 0.3  
        // ,
        if (this.dotCount < 0) {
          this.dotCount = 0;
          this.needCountdown = false;
          return;
        }
        this.countingdown = true;
        this.delayTimer = setTimeout(() => {
          this.countdown().then(()=> {
            //  
            //setTimeout(() => {
              this.countingdown = false;
              if(this.mediaRecorder.state !== "recording") {
                this.mediaRecorder.start();
                this.recordStarttime = this.$refs.audio.currentTime;
                // 
                this.removeRecordedSegmentsAfter(this.recordStarttime);
              }
            //}, 300);
          });
        }, delay);
        this.needCountdown = false;
        return;
      }
      if(this.countingdown){
        return;
      }
      // 
      for (let i = 0; i < this.store.parsedLyrics.length; i++) {
        if (
          currentTime >= this.store.parsedLyrics[i].time &&
          (i === this.store.parsedLyrics.length - 1 ||
            currentTime < this.store.parsedLyrics[i + 1].time)
        ) {
          this.curVal = [this.store.parsedLyrics[i].value]; // 
          break;
        }
      }
    },

    // ,  , , 
    onChange({ selectedValues }) {
      // if(this.changeTimer) {
      //   clearTimeout(this.changeTimer);
      //   this.changeTimer = null;
      // }
      //stop ondataavailable , 
      // this.mediaRecorder.stop();
      //this.changeTimer = setTimeout(() => {
        // if(this.scrolling){
        //   return;
        // }
        // console.log("stop")
        // //showToast(`: ${selectedValues.join(",")}`);
        // //this.setRecordAudio(this.touchTime, selectedValues[0])

        // // this.touchTime = null;
        // let n = selectedValues[0] - 5;
        // if(n < 0){
        //   n = 0;
        // }
        // this.$refs.audio.currentTime = n;
        // this.setCountdownTime(selectedValues[0]);
      //}, 500);
      if(this.mediaRecorder.state === "recording") {
        this.mediaRecorder.stop();
      }
      this.valueChangeTime = selectedValues[0];

    },

    changeTime() {
      if(this.scrolling || !this.valueChangeTime){
        return;
      }
      //console.log("stop")
      //showToast(`: ${selectedValues.join(",")}`);
      //this.setRecordAudio(this.touchTime, selectedValues[0])

      // this.touchTime = null;
      // , , 
      let n = this.valueChangeTime - 5;
      if(n < 0){
        n = 0;
      }
      console.log("n-> ", n);
      this.$refs.audio.currentTime = n;
      this.setCountdownTime(this.valueChangeTime);
      this.valueChangeTime = null;
    },

    // 
    countdown() {
      // this.dotCount = n;
      return new Promise((rs) => {
        this.countingdown = true;
        let fun = () => {
          if (this.dotCount > 0) {
            this.cdTimer = setTimeout(() => {
              this.dotCount--;
              fun();
            }, 1000);
          } else {
            rs();
          }
        };
        fun();
      });
    },
    // 
    onReplay() {
      if(this.countingdown == true) {
        return;
      }
      showConfirmDialog({
        title: '',
        message:'?',
      }).then(() => {
        this.doReplay();
      })
      .catch(() => {
        // on cancel
      });
    },
    // 
    doReplay() {
      this.clearTimer();
      this.$refs.audio.pause();
      this.$refs.audio.currentTime = 0;
      this.countingdown = false;
      this.dotCount = 5;
      this.valueChangeTime = null;
      this.showCountdownTime = null;
      if(this.store.lrc && this.store.parsedLyrics.length) {
        this.curVal = [this.store.parsedLyrics[0].value]
        //this.needCountdown = true;
        this.setCountdownTime(this.store.parsedLyrics[0].value);
      }
      
      //  stop
      // ,
      this.mediaRecorder.ondataavailable = ()=>{};
      this.mediaRecorder.stop();
      this.setMediaRecorder();
      this.recordStarttime = 0;
      this.store.audioChunk = [];
      this.mediaRecorder.start();
      this.drawWaveform();
      this.$refs.audio.play();
      this.playing = true;
      this.checkQianzouLength();
    },
    // 
    onFinish() {
      if(this.countingdown == true) {
        return;
      }      
      console.log('onFinish');
      if(this.$refs.audio.paused) {
        this.isAlreadyPaused = true;
      }
      else {
        this.$refs.audio.pause();
        this.isAlreadyPaused = false;
      }
      // 
      this.fShow = true;
    },
    finish() {
      console.log('finish');
      this.$refs.audio.pause();
      // stop  onavailable?? 
      this.mediaRecorder.stop();
      setTimeout(() => {
        this.$router.push({
          path: '/edit',
        });
      }, 50);

    },
    onFshowCancel() {
      this.fShow = false;
      //   .... ...
      if(!this.isAlreadyPaused) {
        this.$refs.audio.play();
      }
    },
    onFshowSelect(action, index) {
      console.log(action, index);
      //  this.store.recordLength = this.$refs.audio.currentTime;
      if(index == 0) {
        this.store.recordLength = this.$refs.audio.currentTime;
      }
      else if(index == 1) { // ,  
        this.store.recordLength = 0;
      }
      this.finish();
      
    }
  },
};
</script>
<style scoped>
