<template>
  <van-dialog v-model:show="isVisible" title="" @close="onCloseDialog">
    <div class="c-setting-box">
      <van-cell title="" :value="store.lrcDelayTime" />
      <div class="c-setting-sliderbox">
        <van-slider
          v-model="store.lrcDelayTime"
          @change="onlrcDelayChange"
          min="-2"
          max="2"
          step="0.1"
        />
      </div>
      <van-cell title="" />
      <div class="c-setting-sliderbox">
        <van-radio-group v-model="store.lrcEncoding" direction="horizontal" @change="onLrcEncodingChange">
          <van-radio name="GBK">GBK</van-radio>
          <van-radio name="UTF-8">UTF-8</van-radio>
        </van-radio-group>
      </div>   
      <van-cell title="" :value="parseInt(store.banzouVolume*100)" />
      <div class="c-setting-sliderbox">
        <van-slider
          v-model="store.banzouVolume"
          @change="onBanzouVolumeChange"
          min="0"
          max="1"
          step="0.1"
        />
      </div>
      <van-cell title="" :value="parseInt(store.banzouPitch * 10)" />
      <div class="c-setting-sliderbox">
        <van-slider
          v-model="store.banzouPitch"
          @change="onBanzouPitchChange"
          min="-1.1"
          max="1.1"
          step="0.1"
        />        
      </div>         
    </div>
  </van-dialog>    
</template>

<script>
import useStore from "../store";
import { ref, watch } from "vue";
export default {
  name: "setting",
  props: {
    visible: { type: Boolean, default: false },
  },
  emits: ['update:visible'],  
  setup(props, { emit }) {
    // store,
    const store = useStore();
    const isVisible = ref(props.visible);  
  
    // propsvisibleisVisible  
    watch(() => props.visible, (newVal) => {  
      console.log(newVal);
      isVisible.value = newVal;  
    });   
    //  
    let onCloseDialog = ()=> {  
      console.log('');
      emit('update:visible', false);  
    } 
    return { store, isVisible, onCloseDialog };
  },
  methods: {
    onlrcDelayChange (val) {
      // 
      this.store.parseLRC()
    },
    onLrcEncodingChange (val) {
      //  
      this.store.readLrcText()
    },
    onBanzouVolumeChange(val) {
      this.store.banzouVolume = val;
    },
    onBanzouPitchChange(val) {
      this.store.banzouPitch = val;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
