import { defineStore } from "pinia";

const useStore = defineStore("userData", {
  state: () => {
    return {
      // test
      counter: 0,
      // 
      banzou: null,
      // 
      lrcEncoding: "GBK",
      // file
      lrc: null,
      // lrc
      lrcText: "",
      //
      parsedLyrics: [],
      // 
      lrcDelayTime: 0,
      // 
      banzouLength: 0,
      //  0, >0
      recordLength: 0,
      // 
      banzouVolume: 0.7,
      // 
      humanVolume: 0.9,
      // 
      banzouPitch: 0,
      // 
      audioChunk: [],
      // 
      reverb: false,
      // 
      voiceDelay: 0,
      //mp3
      compress: false,
      // 
      loading: false,
      // 
      rendering: false,
      // 
      renderingProgess: 0,
      //alert
      alertDialog: {
        show: false,
        text: "",
      },
      // 
      reverbOption: {
        time: 1.8,
        decay: 1.5,
        mix: 0.4
      },
      //   !!!
      reverbOption_default: {
        time: 1.8,
        decay: 1.5,
        mix: 0.4
      },      
    };
  },
  actions: {
    parseLRC() {
      //console.log(" " + this.lrcText);
      let lrcContent = this.lrcText;
      if (!lrcContent) {
        console.log("");
        return;
      }
      const lines = lrcContent.split(/[\r\n]/);
      const timeExp = /\[(\d{2}):(\d{2})\.(\d{2,3})\]/; //  [mm:ss.xx]
      const lyricsList = [];

      lines.forEach((line) => {
        const match = timeExp.exec(line);
        if (match) {
          const minutes = parseInt(match[1], 10);
          const seconds = parseInt(match[2], 10);
          let milliseconds = parseInt(match[3], 10);
          milliseconds = match[3].length > 2 ? parseInt(match[3], 10) / 1000 : parseInt(match[3], 10) / 100;
          const timeInSeconds = minutes * 60 + seconds + milliseconds;

          const lyricText = line.replace(timeExp, "").trim();

          // Picker  timevalue,ai,,...
          lyricsList.push({
            text: lyricText,
            value: timeInSeconds + this.lrcDelayTime,
            time: timeInSeconds + this.lrcDelayTime,
          });
        }
      });

      this.parsedLyrics = lyricsList;
    },
    // 
    readLrcText() {
      return this.readText().then((txt) => {
        //console.log(" " + txt);
        this.lrcText = txt;
        this.parseLRC();
      });
    },

    readText() {
      if (!this.lrc) {
        console.log("");
        return;
      }
      return new Promise((rs) => {
        let fr = new FileReader();
        fr.onload = (e) => {
          rs(e.target.result);
        };
        fr.readAsText(this.lrc, this.lrcEncoding);
      });
    },

    // test
    increment() {
      this.counter++;
    },

    // loading
    showLoading() {
      this.loading = true;
    },

    hideLoading() {
      this.loading = false;
    },
    // render progess
    showProgess(n) {
      this.rendering = true;
      this.renderingProgess = n;
    },

    hideProgess() {
      this.rendering = false;
      setTimeout(() => {
        this.renderingProgess = 0;
      }, 1000);
    },
    showAlert(txt) {
      this.alertDialog.show = true;
      this.alertDialog.text = txt;
    },

    hideAlert() {
      this.alertDialog.show = false;
      this.alertDialog.text = "";
    },
  },
  // 
  persist: {
    paths: ["lrcDelayTime","lrcEncoding","compress", "reverb", "reverbOption", "voiceDelay", "banzouVolume", "humanVolume"],
  },
});

export default useStore;
