<template>
  <div class="container home">
    <van-nav-bar
      title=""
      left-text=""
      @click-left="showAbout()"
    />
    <div class="content c-text-center">
      <!-- <van-button type="primary" @click="test1()">test</van-button>
      <img alt="Vue logo" src="../assets/logo.png" />
      <div class="c-testbox"></div>
      <van-picker
        title=""
        :columns="columns"
        @confirm="onConfirm"
        @cancel="onCancel"
        @change="onChange"
      /> -->
      <div class="c-select-title c-mtop15"></div>
      <div class="c-iconadd-box">
        <input
          type="file"
          ref="banzou"
          style="display: none"
          @change="onBanzouFileChange"
        />
        <van-icon
          name="plus"
          class="c-img-add"
          v-show="store.banzou == null"
          @click="onAddBanzou()"
        />
        <img
          :src="banzouImg"
          class="c-icon-selected"
          v-show="store.banzou != null"
        />
        <van-icon
          name="cross"
          class="c-icon-rev"
          v-show="store.banzou != null"
          @click="onRevBanzou"
        />
        <div class="c-text-selected" v-show="store.banzou != null">
          {{ store.banzou && store.banzou.name }}
        </div>
      </div>
      <div class="c-select-title">(lrc)</div>
      <div class="c-iconadd-box">
        <input
          type="file"
          ref="lrc"
          style="display: none"
          @change="onLrcFileChange"
        />
        <van-icon
          name="plus"
          class="c-img-add"
          v-show="store.lrc == null"
          @click="onAddLrc()"
        />
        <img :src="lrcImg" class="c-icon-selected" v-show="store.lrc != null" />
        <van-icon
          name="cross"
          class="c-icon-rev"
          v-show="store.lrc != null"
          @click="onRevLrc"
        />
        <div class="c-text-selected" v-show="store.lrc != null">
          {{ store.lrc && store.lrc.name }}
        </div>
      </div>
      <div>
        <van-button
          type="primary"
          size="large"
          class="c-rec-btn"
          @click="onStartRec()"
          ></van-button
        >
        <van-button plain type="danger" class="c-compose-btn"  @click="onstartCompose()"></van-button>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import useStore from "../store";
import { showToast, showDialog } from "vant";
export default {
  name: "HomeView",
  components: {},
  setup() {
    const store = useStore();
    return {
      store,
    };
  },
  data() {
    return {
      banzouImg: require("../assets/banzou.png"),
      lrcImg: require("../assets/lrc.png"),
    };
  },
  mounted() {},
  methods: {
    test1() {
      // this.$router.push("/about");
      //const store = useStore();
      // console.log(store.counter);
      // store.counter++;
      console.log(this.$refs.banzou);
    },
    showAbout() {
      showDialog({
        title: "",
        message: "Author: MikeChen",
        showConfirmButton: true,
      });
    },
    onClickRight() {
      // this.store.showLoading();
      this.$router.push("/compose");
    },
    isSurpportFile(file) {
      const regex = /\.(mp3|wav)$/i; // .mp3 .wav
      return regex.test(file.name);
    },
    isLrcFile(file) {
      const regex = /\.lrc$/i; // .lrc
      return regex.test(file.name);
    },
    onStartRec() {
      if (this.store.banzou == null) {
        showToast("");
        return;
      }
      this.$router.push("/record");
    },
    onstartCompose() {
      if (this.store.banzou == null) {
        showToast("");
        return;
      }
      this.$router.push("/compose");
    },
    onAddBanzou() {
      this.$refs.banzou.click();
    },
    onBanzouFileChange() {
      const file = this.$refs.banzou.files[0];
      // console.log(file);
      if (!this.isSurpportFile(file)) {
        showToast(" wav, mp3 ");
        return;
      }
      this.store.banzou = file;
    },
    onRevBanzou() {
      this.store.banzou = null;
      this.$refs.banzou.value = "";
    },
    onLrcFileChange() {
      const file = this.$refs.lrc.files[0];
      // console.log(file);
      if (!this.isLrcFile(file)) {
        showToast(" lrc ");
        return;
      }
      this.store.lrc = file;
    },
    onAddLrc() {
      this.$refs.lrc.click();
    },
    onRevLrc() {
      this.store.lrc = null;
      this.$refs.lrc.value = "";
    },
  },
};
</script>
